import colors from './colors.json';
import '../css/colorpicker.css';
import '../css/style.css';


const paletteContainer = document.querySelector('.js-palette');
const cardsMarkup = createColorCardsMarkup(colors);

paletteContainer.insertAdjacentHTML('beforeend', cardsMarkup);

paletteContainer.addEventListener('click', onPaletteContainerClick);
//---------------------------------------------------------------------
function createColorCardsMarkup (colors) {
    return colors
    .map(({hex, rgb}) => {
        return `
        <div class="color-card">
                <div 
                    class="color-swatch" 
                    data-hex="${hex}" 
                    data-rgb="${rgb}" 
                    style="background-color: ${hex};"
                ></div>
                <div class="color-meta">
                    <p>HEX: ${hex}</p>
                    <p>RGB: ${rgb}</p>
                </div>
            </div>
        `;
    }).join('');
    // console.log(markup);
}
//---------------------------------------------------------------------
function onPaletteContainerClick (evt) {
    const isColorSwatchEl = evt.target.classList.contains('color-swatch');

    if (!isColorSwatchEl) {                               // —á—Ç–æ–± –∫–ª–∏–∫ –±—ã–ª —Ç–æ–ª—å–∫–æ –Ω–∞ —Ü–≤–µ—Ç–µ
        return;
    }

    const swatchEl = evt.target;
    const parentColorCard = swatchEl.closest('.color-card');

    removeActiveCardClass();
    addActiveCardClass(parentColorCard);
    setBodyBgColor(swatchEl.dataset.hex);
}
//---------------------------------------------------------------------
function setBodyBgColor (color) {          // –∑–∞–¥–∞–µ—Ç—Å—è —Ü–≤–µ—Ç –±–æ–¥–∏
    document.body.style.backgroundColor = color;
}
//---------------------------------------------------------------------
function removeActiveCardClass () {             // —Å–Ω–∏–º–∞–µ—Ç—Å—è –∞–∫—Ç–∏–≤–Ω–∞—è –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –¥—Ä—É–≥–æ–º —Ü–≤–µ—Ç–µ
    const currentActiveCard = document.querySelector('.color-card.is-active');

    if (currentActiveCard) {
        currentActiveCard.classList.remove('is-active');
    }
}
//---------------------------------------------------------------------
function addActiveCardClass (card) {         // –¥–æ–±–∞–≤–∏—Ç—å –∞–∫—Ç–∏–≤ –Ω–∞ —Ü–≤–µ—Ç
    card.classList.add('is-active');
}  
//---------------------------------------------------------------------
